* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Oswald", "sans-serif";
}

/* ELEMENT STYLING */

ul {
  list-style: none;
}
h1 {
  margin: 20px auto;
  font-weight: 600;
}
li {
  display: grid;
  grid-template-columns: 250px 150px 50px;
  gap: 16px;
  padding: 10px;
}
button {
  border-radius: 5px;
  border: none;
  font-size: 14px;
  height: 30px;
  width: auto;
  text-align: center;
}

/* HEADER AND FOOTER */

.header {
  display: grid;
  align-items: center;
  justify-content: center;
}
.footer {
  font-size: 18px;
  text-align: center;
  display: grid;
}
.loader {
  text-align: center;
}

/* MAIN LAYOUT */
.main {
  display: grid;
  align-items: center;
  justify-content: center;
}
.addButton {
  background-color: rgb(0, 109, 0);
  color: white;
}
.deleteButton {
  background-color: brown;
  color: white;
}

@media (max-width: 900px) {
  .main {
    grid-template-columns: 1fr;
    height: auto;
  }
}
